import produks from '@/databases/produk.json'
const produk = {
  namespaced: true,
  state: {
    produks: produks,
    loading: false,
    detail: {}
  },
  getters: {
    getDatas (state) {
      return state.datas
    }
  },
  mutations: {
    SET_DETAIL (state, data) {
      state.detail = data
    },
    SET_LOADING (state, loading) {
      state.loading = loading
    }
  },
  actions: {
    async toDetail ({ commit }, payload) {
      commit('SET_LOADING', true)
      commit('SET_DETAIL', payload)
      alert(JSON.stringify(payload))
      // return await AuthenticationService.get('menus/menu').then(({ data }) => {
      //   commit('SET_DATAS', data.data)
      //   commit('SET_LOADING', false)
      // })
    }
  }
}

export default produk
